(function() {
  'use strict';

  angular
    .module('shico')
    .controller('ParameterIOController', ParameterIOController);

  function ParameterIOController(TrackerParametersService) {
    var vm = this;
    vm.getParameters = getParameters;
    vm.setParameters = setParameters;
    vm.closeParamIO = closeParamIO;

    vm.text = '';
    vm.readOnly = false;
    vm.hide = true;

    function getParameters () {
      vm.hide = false;
      vm.readOnly = true;
      vm.text = JSON.stringify(TrackerParametersService.getParameters());
      vm.btnText = 'Ok';
    }

    function setParameters () {
      vm.hide = false;
      vm.readOnly = false;
      vm.text = '';
      vm.btnText = 'Load';
    }

    function closeParamIO() {
      vm.hide = true;
      if(!vm.readOnly) {
        var params = JSON.parse(vm.text);
        TrackerParametersService.setParameters(params);
      }
    }
  }
})();

(function() {
  'use strict';

  angular
      .module('shico')
      .service('GraphControlService', GraphControlService);

  function GraphControlService(GraphConfigService) {
    var vm = this;

    vm.streamGraph = {
      options: GraphConfigService.getConfig('streamGraph'),
      data:    []
    };

    vm.forceGraph = {
      options: GraphConfigService.getConfig('forceGraph'),
      data: [],
      currYearIdx: 0
    };

    vm.yearLabels = [];
    vm.slider_options = {
      floor: 0,
      ceil: 0,
      showTicksValues: false,
      translate: getYearLabel
    };

    var service = {
      update: update,
      getRawData: getRawData,
      getYearLabel: getYearLabel,
      streamGraph: vm.streamGraph,
      forceGraph:  vm.forceGraph,
      slider_options: vm.slider_options

    };
    return service;

    function getYearLabel(yearIdx) {
      return vm.yearLabels[yearIdx];
    }

    // Update graphs with the given data
    function update(data) {
      vm.rawData = data;

      // Collect all words and year labels on data
      var allYears = [];
      var allWords = new Set();
      angular.forEach(data.stream, function(wordValues, year) {
        allYears.push(year);
        angular.forEach(wordValues, function(weight, word) {
          allWords.add(word);
        });
      });

      // Create year idx -> label table
      var yearIdx = {};
      angular.forEach(allYears, function(year, idx) {
        yearIdx[year] = idx;
      });

      // Register vocabulary and year labels with to be used by config
      GraphConfigService.setVocabulary(allWords);
      GraphConfigService.setStreamYears(allYears);
      vm.yearLabels = allYears;

      // Prepare data on format suitable from NVD3
      var streamData = formatForStream(data.stream, yearIdx, allWords, allYears);
      var forceData  = formatForForce(data.networks, yearIdx);

      // Register data on graph
      vm.streamGraph.data = streamData;
      vm.forceGraph.data = forceData;

      vm.slider_options.ceil = vm.yearLabels.length-1;
    }

    function getRawData() {
      return vm.rawData;
    }

    function formatForStream(data, yearIdx, allWords, allYears) {
      var streamData = [];
      angular.forEach(allWords, function(word) {
        var values = [];
        angular.forEach(allYears, function(year) {
          var val = (word in data[year]) ? data[year][word] : 0;
          this.push([ yearIdx[year], val]);
        }, values);
        this.push({
          key: word,
          values: values
        });
      }, streamData);
      return streamData;
    }

    function formatForForce(data, yearIdx) {
      var forceData = {};

      angular.forEach(data, function(network, year) {
        forceData[yearIdx[year]] = network;
      });

      return forceData;
    }
  }
})();

(function() {
  'use strict';

  angular
      .module('shico')
      .service('GraphConfigService', GraphConfigService);

  function GraphConfigService() {
    // NVD3 configuration for stream graph
    var streamConfig = {
      chart: {
          type: 'stackedAreaChart',
          height: 500,
          margin : {
              top: 20,
              right: 20,
              bottom: 60,
              left: 55
          },
          x: getX,
          y: getY,
          xAxis: {
            tickFormat: tickYear
          },
          yAxis: {
            tickFormat: tickY
          },
          color: getColour
      }
    };

    var customSymbol = d3.svg.symbol()
                .type( chooseSymbolType )
                .size( chooseSymbolSize );

    // NVD3 configuration for force directed graph
    var forceConfig = {
      chart: {
          type: 'forceDirectedGraph',
          height: 300,
          width: 300,
          color: getColour,
          symbol: customSymbol,
          nodeExtras: processNode,
          curveLinks:  true,
          useArrows: true,
          tooltip: { contentGenerator: customTooltipContent }
      }
    };
    var colours = d3.scale.category20();

    var yearTickLabels = {};   // Year markers for stream graph
    var forceGraphHooks = [];
    var wordColourIdx = {};

    var service = {
      getConfig: getConfig,
      setStreamYears: setStreamYears,
      addForceGraphHook: addForceGraphHook,
      setVocabulary: setVocabulary
    };
    return service;

    // Helper functions for all graphs
    function setVocabulary(vocab) {
      var idx = 0;
      // Each word of vocabulary is assigned an unique ID, later used to assign colour
      // TODO: can't we get a list of words from server?
      angular.forEach(vocab, function(word) {
        wordColourIdx[word] = idx;
        idx += 1;
      });
    }

    function getColour(item) {
      var word = item.key || item.name;
      var cIdx = wordColourIdx[word];
      return cIdx ? colours(cIdx) : '#223344';
    }

    // Helper functions for streamConfig
    function getX(point){ return point[0]; }
    function getY(point){ return point[1]; }
    function tickY(tickVal) { return parseFloat(tickVal).toFixed(1); }

    function tickYear(idx) {
      if(idx in yearTickLabels) { return yearTickLabels[idx]; }
      else { return idx; }
    }
    function setStreamYears(labels) {
      yearTickLabels = labels;
    }

    // Helper functions for forceConfig
    function processNode(node) {
      // Nodes have: {'name': 'str', 'type': 'seed', 'count': N, year: XXXX},
      addTextLabels(node);
      setSize(node);
      angular.forEach(forceGraphHooks, function(hook) {
        hook(node);
      });
    }

    function addTextLabels(node) {
      node.append("text")
        .attr("dx", 12)
        .attr("dy", ".35em")
        .text(function(d) { return d.name; })
        .style("font-weight", function(d) { return d.type=="seed"?"bold":""; });
    }

    function setSize(node) {
      node.select('circle')
        .attr('r', function(d) { return 5 + 2 * (d.count); });
    }

    function chooseSymbolType(d) {
      if (d.type=="seed") {
        return "triangle-up";
      } else if(d.type=="word") {
        return "circle";
      } else {
        return "diamond";
      }
    }

    function chooseSymbolSize(d) {
      return 50 * Math.log2(2 + d.count);
    }

    function customTooltipContent(d) {
      var showContent = [ 'type', 'count', 'weight' ];

      var html = '<table>';
      html += '<thead><tr><td colspan="2"><strong class="x-value">' + d['name'] + '</strong></td></tr></thead>';
      html += '<tbody>';

      angular.forEach(showContent, function(key) {
        html += '<tr>';
        html += '<td class="key">' + key + '</td>';
        html += '<td class="value">' + d[key] + '</td>';
        html += '</tr>';
      });

      html += '</tbody>';
      html += '</table>';
      return html;
    }

    function addForceGraphHook(callback) {
      forceGraphHooks.push(callback);
    }

    function getConfig(graphName) {
      if(graphName === 'streamGraph') {
        return streamConfig;
      } else if(graphName === 'forceGraph'){
        return forceConfig;
      }
    }
  }
})();

(function() {
  'use strict';

  angular
      .module('shico')
      .controller('FormController', FormController);

  function FormController(ConceptService,
                          TrackerParametersService,
                          GraphControlService) {
    var vm = this;

    vm.doPost = doPost;

    function doPost() {
      var params = TrackerParametersService.getParameters();
      var resp = ConceptService.trackConcept(params);
      vm.trackerPromise = resp; // ng-busy watches on trackerPromise
      resp.then(GraphControlService.update);
    }
  }
})();

(function() {
  'use strict';

  angular
      .module('shico')
      .service('SettingsService', SettingsService);

  function SettingsService($resource, TrackerParametersService) {
    var service = {
      doLoad: doLoad
    };
    return service;

    function doLoad() {
      var configFile = $resource('config.json');
      configFile.get().$promise.then(function(config) {
        var baseURL = config.baseURL;
        var trackerURL = baseURL + '/track/:terms';
        var avlYearSvcURL = baseURL + '/available-years';

        if(baseURL.length==0) {
          trackerURL = 'dummy2.json';
          avlYearSvcURL = 'http://localhost:5000/available-years';
        }

        // Copy config from JSON to the service
        service.trackerURL = trackerURL;

        // Call avlYearSvc resource to get years
        var avlYearResource = $resource(avlYearSvcURL);
        avlYearResource.get().$promise.then(function(years) {
          TrackerParametersService.availableYears.from = years.first;
          TrackerParametersService.availableYears.to = years.last;
          TrackerParametersService.availableYears.values = years.values;
          TrackerParametersService.availableYears.options.floor = years.first;
          TrackerParametersService.availableYears.options.ceil = years.last;
        });
      });
    }
  }
})();

(function() {
  'use strict';

  angular
      .module('shico')
      .service('ConceptService', ConceptService);

  function ConceptService($resource, $log, SettingsService) {
    var service = {
      trackConcept: trackConcept
    };
    return service;

    function trackConcept(trackerParams) {
      // trackerURL format: http://localhost:5000/track/:terms
      var tracker = $resource(SettingsService.trackerURL);
      var request = tracker.get(trackerParams);
      var trackPromise = request.$promise.then(parseTermTrack);
      return trackPromise;
    }

    function parseTermTrack(data) {
      // If data needs to be parsed, it should be done here.

      // Copy year into each node
      angular.forEach(data.networks, function(net,year) {
        angular.forEach(net.nodes, function(node) {
          node.year = year;
        });
      });
      return data.toJSON();
    }
  }
})();

